<template>
  <span>{{ productFormat }}</span>
</template>

<script>
export default {
  computed: {
    productFormat() {
      const number = this.$store.state.cartProducts.length.toString().slice(-1);
      if (number === '1') {
        return 'товар';
      } if (number === '2' || number === '3' || number === '4') {
        return 'товара';
      } return 'товаров';
    },
  },
};
</script>
