<template>
  <div class="form__counter">
    <button type="button" aria-label="Убрать один товар" @click.prevent="removeProduct">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>

    <input type="text" v-model.number="counter" required>

    <button type="button" aria-label="Добавить один товар" @click.prevent="counter++">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script>
export default {
  props: ['data'],
  data() {
    return {
      counter: this.data,
    };
  },
  watch: {
    counter() {
      this.sendEvent();
    },
  },
  methods: {
    removeProduct() {
      if (this.counter > 1) this.counter += 1;
    },
    sendEvent() {
      this.$emit('update:data', this.counter);
    },
  },
};
</script>
